.section-testimonials {
	@extend %sectionBackground;
	background-image: url("../images/background-4.jpg");
	&__testimonial {
		margin:0;
	}
	&__image {
		display: block;
		margin: 0 auto;
		border-radius: 50%;
	}
	&__text {
		display: block;
		max-width: 80%;
		margin: 2rem auto 1.75rem;
	}
}